var listsNested = document.querySelectorAll('.paragraph-list__lists_nested');
var listsText = document.querySelectorAll('.paragraph-list__text-link');

function hiddenLists() {
  for(var i = 0; i < listsNested.length; i++) {
    listsNested[i].classList.add('hidden');
  }

  for(var i = 0; i < listsText.length; i++) {
    listsText[i].classList.remove('paragraph-list__text-link_active');
  }
}

function openLists(e){
  if(e.target.classList.contains('paragraph-list__text-link_arrow')) {
    e.target.parentNode.querySelector('.paragraph-list__lists_nested').classList.toggle('hidden');
  }
  e.target.classList.toggle('paragraph-list__text-link_active');
}

function openHeader(burger){
  burger.classList.toggle('header__burger_active');
  burger.parentNode.classList.toggle('header__wraper_open');
  burger.parentNode.querySelector('.header__container').classList.toggle('header__container_hidden_mob');
}

function mobileOpenHeader(e){
  if(e.target.classList.contains('header__burger')) {
    var burger = e.target;
    openHeader(burger);
  }
  if(e.target.parentNode.classList.contains('header__burger')) {
    var burger = e.target.parentNode;
    openHeader(burger);
  }
  if(e.target.classList.contains('paragraph-list__text-link')){
    if(lastClickElement === e.target) {
      hiddenLists();
      lastClickElement = null;
    } else {
      hiddenLists();
      openLists(e);
      lastClickElement = e.target;
    }
  }
}

if(getComputedStyle(document.querySelector('.header__burger')).display !== 'block') {
  document.addEventListener('mouseover', function(e) {
    if(e.target.classList.contains('paragraph-list__text-link')) {
      hiddenLists();
      openLists(e);
    }
  });
  document.addEventListener('mouseover', function(e) {
    if(e.target.classList.contains('logo') || e.target.classList.contains('main')){
      hiddenLists();
    }
  });
} else {
  var lastClickElement;
  document.addEventListener('touchstart', function(e) {
    mobileOpenHeader(e);
  });
  document.addEventListener('click', function(e) {
    mobileOpenHeader(e);
  });
}


